button:focus  {
    outline: 0;
}

/*date toggle*/
.date-dropbtn {
    border-radius: 18px;
    background-color: grey;
    color: #4A4A4A;
    padding: 6px 10px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    margin-bottom: 10px;
}

.date-dropbtn i {
    border-radius: 20px;
    margin: 0;
    border-color: black;
    color: white;
    background-color: black
}

.date-dropbtn i:hover {
    background-color: black;
    color: white;
}
.date-dropbtn:hover, .date-dropbtn:focus {
    background-color: #979797;
    color: black;
    font-weight: 500;
}

.date-dropdown {
    position: absolute;
    display: inline-block;
}

.date-dropdown-content {
    display: none;
    position: absolute;
    background-color: white;
    min-width: 300px;
    overflow: auto;
    box-shadow:0 2px 4px 0 rgba(0, 0, 0, 0.5);
    z-index: 1;
    left: 0;
}

.date-dropdown-content p {
    padding: 6px 16px;
    display: block;
    margin: 12px;
    cursor: pointer;
}

.date-dropdown p:hover, .date-picked {
    background-color: black;
    color: white;
    border-radius: 4px;
}

.date-dropdown-content button {
    border-radius: 18px;
    background-color: whitesmoke;
    font-size: 16px;
    border-color: black;
    border-style: solid;
    cursor: pointer;
    padding: 6px 16px;
    margin: 12px 10px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.date-dropdown-content button:nth-child(1) {
    background-color: red;
    color: white;
    border-color: red;
}

.date-dropdown-content button:hover {
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.2);
}

